<template>
  <div
      class="intro-wrap"
      :class="{ 'animate-visible': isVisible }"
      ref="intro"
  >

    <div class="scroll-line" :class="{ 'line-visible': isVisible }"></div>

    <!-- 타이틀 제목 -->
    <div class="content-title">
      {{section.title}}
    </div>

    <!-- 내용 -->
    <div class="content">
      <p>{{ section.description[0]?.line_1 }}</p>
      <p>{{ section.description[0]?.line_2 }}</p>
      <p>{{ section.description[0]?.line_3 }}</p>
      <p>{{ section.description[0]?.line_4 }}</p>
    </div>

    <div class="content-update" v-if="update" >
      <button @click="toggleEdit">
        {{ isEditing ? "저장하기" : "수정하기" }}
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "introDefault",
  data() {
    return {
      isVisible: false, // 애니메이션 트리거
      isEditing: false,
      editedSection: {}, // 수정된 데이터
    };
  },
  props: {
    section: {
      type: [Array, Object],
      default: () => ([]),
    },
    update:{
      type: Boolean,
    },
    index:{
      type: Number
    }
  },
  mounted() {
    this.editedSection = JSON.parse(JSON.stringify(this.section)); // 데이터 복사
    const observer = new IntersectionObserver(
        ([entry]) => {
          if (entry.isIntersecting) {
            this.isVisible = true; // 애니메이션 활성화
            observer.disconnect(); // 중복 실행 방지
          }
        },
        { threshold: 0.1 } // 10%가 보이면 트리거
    );

    observer.observe(this.$refs.intro);
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.handleScroll); // 이벤트 제거
  },
  methods: {
    toggleEdit() {
      if (this.isEditing) {
        // 저장 버튼 클릭 시 부모로 변경된 데이터 전달
        this.$emit("edit-section", this.index, this.editedSection);
      }
      this.isEditing = !this.isEditing;
    },
  },
};
</script>
<style scoped>
/* 애니메이션 */
.intro-wrap {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 1.5s ease, transform 1.5s ease;
  justify-self: center;
  padding-bottom: 32px;

  /* 스크롤 라인 */
  .scroll-line {
    justify-self: center;
    width: 0.6px;
    height: 0;
    background: #191c21;
    border-radius: 2px;
    transition: height 1.5s ease;
  }

  .scroll-line.line-visible {
    height: 52px;
  }

  .content-title{
    color: #191c21;
    font-weight: 700;
    font-size: 21px;
    text-align: center;
    padding-top: 32px;
  }

  /* 컨텐츠 영역 */
  .content {
    padding: 24px 24px 0;
    p{
      color: #191c21;
      text-align: center;
      font-weight: 400;
      font-size: 16px;
      line-height: 1.8;
      margin-bottom: 6px; /* 내용 블록 아래 간격 추가 */
    }
  }

}

.intro-wrap.animate-visible {
  opacity: 1;
  transform: translateY(0);
}

/* 인풋박스 공통 */
input {
  text-align: center;
  width: 100%;
  font-size: 14px;
}
input:hover{
  border: 0.6px solid #b0b0b0;
}
</style>